@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Regular/OpenSans-Regular.eot?v=1.1.0");
  src: url("../fonts/Regular/OpenSans-Regular.eot?#iefix&v=1.1.0") format("embedded-opentype"), url("../fonts/Regular/OpenSans-Regular.woff2?v=1.1.0") format("woff2"), url("../fonts/Regular/OpenSans-Regular.woff?v=1.1.0") format("woff"), url("../fonts/Regular/OpenSans-Regular.ttf?v=1.1.0") format("truetype"), url("../fonts/Regular/OpenSans-Regular.svg?v=1.1.0#Regular") format("svg");
  font-weight: 400;
  font-style: normal; }
  
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Bold/OpenSans-Bold.eot?v=1.1.0");
  src: url("../fonts/Bold/OpenSans-Bold.eot?#iefix&v=1.1.0") format("embedded-opentype"), url("../fonts/Bold/OpenSans-Bold.woff2?v=1.1.0") format("woff2"), url("../fonts/Bold/OpenSans-Bold.woff?v=1.1.0") format("woff"), url("../fonts/Bold/OpenSans-Bold.ttf?v=1.1.0") format("truetype"), url("../fonts/Bold/OpenSans-Bold.svg?v=1.1.0#Bold") format("svg");
  font-weight: 700;
  font-style: normal; }

*, *:before, *:after{
	margin:0;
	padding:0;
	border:0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);}
html{
	width: 100%;
	height: 100%; overflow:hidden;
	-ms-text-size-adjust:100%;
	-webkit-text-size-adjust:100%;}
body {
	font-family: 'Open Sans', sans-serif, arial;
	font-weight:400;
	line-height:1;
	width:100%;
	background-color:#FFFFFF; overflow:hidden;
	color:#6e6f73;
	height:100%;	
	font-size:100%;
	vertical-align:baseline;}
a{
	text-decoration:none; color:#6e6f73;}
.clear{
	zoom:1}
.clear:before,.clear:after{
	content:" ";
	display:table;}
.clear:after{
	clear:both;}
ul{
	list-style:none;}
input, img, button, a, textarea{
	outline:0;}
a{ text-decoration:none;}
img{
	max-width:100%;
	display:inline-block; vertical-align:middle;}
:disabled{
	opacity:0.3;}
.wrap{
	width:100%;
	margin:0 auto;}

::-webkit-input-placeholder {
 	font-family: 'Open Sans', sans-serif, arial; color: #FFFFFF; font-size:14px;}
:-moz-placeholder {
   	font-family: 'Open Sans', sans-serif, arial; color: #FFFFFF; opacity: 1; font-size:14px;}
::-moz-placeholder {
   	font-family: 'Open Sans', sans-serif, arial; color: #FFFFFF; opacity: 1; font-size:14px;}
:-ms-input-placeholder {
  	font-family: 'Open Sans', sans-serif, arial; color: #FFFFFF; font-size:14px;}
::placeholder {
 	font-family: 'Open Sans', sans-serif, arial; color: #FFFFFF; font-size:14px;}
input:-webkit-autofill { -webkit-box-shadow: 0 0 0px 1000px white inset;}
::-moz-selection {background: #092f5e; color:#FFFFFF;}
::selection {background: #092f5e; color:#FFFFFF;}

label{ width:100%; display:block; padding-bottom:10px;}
input[type="text"]{font-family: 'Open Sans', sans-serif, arial; outline:0;font-size:14px;}
input[type="text"]{background-color:#FFFFFF; padding:10px; width:100%; color:#000000; border:0; height:40px;}

input[type="submit"]{ background-color:#d93d24; width:100%; padding: 0 20px; color:#FFFFFF; font-size:18px; font-weight:300; -webkit-appearance: none; cursor:pointer; border-radius:5px; height:40px; max-width:auto;}
select{-webkit-appearance:none;}
input[type="submit"]:disabled{cursor:default;}
form .row.error, form .row.success{ background-color:#FFFFFF; padding:15px; font-size:14px; margin-bottom:10px; margin-top:10px;}
.error{color:#FF0000;}
.success{ color:#0C3;}
.loginForm,.otpForm{
/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#113060+0,124374+100,123b6b+100 */
background: #113060; /* Old browsers */
background: -moz-linear-gradient(top, #113060 0%, #124374 100%, #123b6b 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top, #113060 0%,#124374 100%,#123b6b 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom, #113060 0%,#124374 100%,#123b6b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#113060', endColorstr='#123b6b',GradientType=0 ); /* IE6-9 */
    height: 100%;
    width: 100%;
}
.loginForm form,.otpForm form{
    display: block;
    width: 100%;max-width: 500px;margin:auto;
    height: auto;
    background-color: white;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 30px;
    padding-top: 95px;
    padding-bottom: 15px;
}
.otpForm{
    display: table;
}
.otpForm form{
    display: table-cell;
    vertical-align: middle;
    background-color: transparent;
    padding: 0;
    max-width: 100%;
}
.otpForm form > div{
    background-color: white;
    padding: 40px 20px;
    width: 90%;max-width: 500px;margin:auto;
    margin: auto;
    border-radius: 5px;
}

.avatarLogo{
    text-align: center;
}
.avatarLogo img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #e4e3e3;
}
.loginForm h5,.otpForm h5{
   font-size: 13px;
    font-weight: 300;
    text-align: center;
    padding-top: 23px; 
}
.loginForm h5 strong,.otpForm h5 strong{
    font-weight: bold;
}
.avatarLogo h3{
    color: #103463;
    line-height: 40px;
    text-transform: uppercase;
    font-size: 29px;
}
.avatarLogo h4{
    font-size: 15px;
    font-weight: 300;  
}
.otpForm .avatarLogo h3{
    font-size: 19px;
    color: #5e5c5c;
}
.field{
    text-align: center;
    padding: 15px;
}
.field input[type='text'],.field input[type='tel']{
    border-bottom: 1px solid #a3a3a3;
    background-color: transparent;
    width: 100%;height: 50px;
    display: block;
    color: black;
    font-size: 15px;
    font-family: 'Open Sans', sans-serif, arial;
    text-align: center;
}
.fieldText h4{
    font-size: 16px;
    text-align: center;
    font-weight: 300;
}
.field input[type='tel']::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color:black;text-align: center;
}
.field input[type='tel']::-moz-placeholder { /* Firefox 19+ */
  color:black;text-align: center;
}
.field input[type='tel']:-ms-input-placeholder { /* IE 10+ */
  color:black;text-align: center;
}
.field input[type='tel']:-moz-placeholder { /* Firefox 18- */
  color:black;text-align: center;
}
.field input[type='submit']{
    width: 80%;
    margin: auto;
    background-color: #103463;
    color: white;
    padding: 10px 16px;
    height: auto;
    margin-top: 15px;
    font-weight: 300;
    font-size: 15px;
    text-transform: uppercase;
    font-family: 'Open Sans', sans-serif, arial;
}
.fieldText a{
    font-weight: bold;
    font-size: 14px;
}