header{ width:100%; position:fixed; top:0; left:0; height:55px; background-color:#0d4084; color:#fff; z-index:100001;}
header .title{text-align:center; line-height:55px;}
header .title span{ font-weight:700;}
header .navIcon { background-color:transparent; background-image: url("../images/icons/settings.png");background-position: center center;background-repeat: no-repeat;background-size: 50px 50px;content: "";height: 55px;position: absolute;right: 0;top: 0;width: 55px; cursor:pointer;}

.langMenu{ position:fixed; width:100%; height:100%; right:0; top:55px; background-color:rgba(9, 47, 94, 0.8); z-index:100;}
.langMenu ul{ position:relative; right:0; width:50%; background-color:#092f5e; top:0; height:100%; overflow:auto; padding-bottom:55px;}
.langMenu li{ padding:10px;}
.langMenu li label{ padding:20px;}

.backButton{ background-image:url(../images/back_button.png); height:55px; width:80px; position:absolute; left:0; top:0; background-repeat:no-repeat; background-size:50px 50px; background-position:center center; z-index:10;}

header .navIcon {
    background-color: transparent;
    background-image: url("../images/icons/settings.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 50px 50px;
    content: "";
    height: 55px;
    position: absolute;
    right: 0;
    top: 0;
    width: 55px;
    cursor: pointer;
}

header .hamburg {
    background-color: transparent;
    background-image: url("../images/icons/ham-menu.png");
    background-position: center center;
    background-repeat: no-repeat;
	background-size: 30px 24px;
    content: "";
    height: 55px;
    position: absolute;
    right: 0;
    top: 0;
    width: 55px;
    cursor: pointer;
}
my-menu{
    width: 100%;
    height: 100%;
    position: fixed;
    right:0px;
    top:0px;
    z-index: 999999999;
    padding-left: 100px;
    transform: translateX(100%);
    transition: all 0.5s ease-in-out; 
    -webkit-transition: all 0.5s ease-in-out; 
    -moz-transition: all 0.5s ease-in-out; 
    -o-transition: all 0.5s ease-in-out; 
}
.showMenu my-menu{
   transform: translateX(0%);
    transition: all 0.5s ease-in-out; 
    -webkit-transition: all 0.5s ease-in-out; 
    -moz-transition: all 0.5s ease-in-out; 
    -o-transition: all 0.5s ease-in-out;    
}
my-menu ul{
    position: relative;
    height: 100%;
    background-color: #092f5e;
    width: 100%;
}
my-menu ul.sidemenu li{
    border-bottom: 1px solid #0d4084;
    padding: 15px;
}
my-menu ul.sidemenu li span{
    color: #fff;
}
my-menu ul.sidemenu li:last-child{

    border-bottom: none;
}
my-menu ul.sidemenu svg,
my-menu ul.sidemenu img{
    width: 40px;
    height: auto;
    vertical-align: middle;
    margin-right: 14px;
    filter: brightness(0) invert(1);
}
.closeOverlay{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    display: none;
    left: 0px;
    top: 0;
}
.showMenu my-menu .closeOverlay{
    display: block;
}