@media only screen and (max-width:420px){
	footer li a{font-size:9px;}
	.pageCounsellingDetail .knowing_section li span{font-size:11px;}
	.pageCounsellingDetail .adapt p{font-size:12px;}
}
@media only screen and (min-width:10em) {
	header{ height:55px;}
	.wrap{ width:96%;}
	.splashWrap .logo img{ left:50%; margin-left:-100px;max-width: 230px;}
	.homeMenu ul{ padding:10px 0 0 10px;}
	.homeMenu ul li{padding:0 10px 10px 0;}
	.langMenu ul{ width:100%;}
	.pageCounsellingDetail uib-accordion .title, .pageNovomixYou uib-accordion span.ng-binding.ng-scope{ font-size:14px;}
	.pageTutorial .tutorialIn{ padding:10px 10px;}
}
@media only screen and (min-width:37.5em) {
	.splashWrap .logo img{ left:50%; margin-left:-150px;max-width:300px;}
	.langMenu ul{ width:100%;}
	.pageCounsellingDetail uib-accordion .title, .pageNovomixYou uib-accordion span.ng-binding.ng-scope{ font-size:16px;}
	.pageTutorial .tutorialIn{ padding:20px;}
}
@media only screen and (min-width:48em) {
	.homeMenu ul{ padding:20px 0 0 20px;}
	.homeMenu ul li{padding:0 20px 20px 0;}
	.langMenu ul{ width:100%;}
}
@media only screen and (min-width:64em) {	
	.wrap{ width:94%;}
}
@media only screen and (min-width:70em) {	
}
@media only screen and (min-width:80em) {
	.wrap{ width:90%;}
}
@media (max-width: 767px){
.homeheader_img{margin:20px 10px 0px;background-size: 350px auto;}
.how_head span{font-size: 15px;}
.how_head span.head_icon{float:right;}
}
@media (max-width: 640px){
.how_split.ng-scope::before{left:-100px;}
}
@media (max-width: 640px) and (orientation : landscape) {
.welcom_img img {width: 38%;}
.splashWrap .logo img{margin-left: -90px;max-width: 170px;}
.welcome_con {padding-top: 20px;padding-bottom: 20px;font-size: 15px;}
.activationForm label{font-size: 15px;}
}
@media (max-width: 550px){
.how_listtype .heading_text h3{width: 70%;}
.welcome_con{font-size:18px;}
.please.ng-scope{ top: 130px;width: 65%;}
}
@media (max-width: 480px){
.how_head span{font-size: 15px;width: 75%;}
.tab_desc h2{font-size: 20px;line-height: 28px;}
.homeheader_img{background-size: 300px auto;padding: 40px 50px;}
.welcome_con{font-size:16px;}
.activationForm label{font-size:16px;}
.welcom_img img {width: 80%;}
.tab_desc h2{font-size:17px;}
.how_split.ng-scope::before{left:-70px;top:53%;}
}
@media (max-width: 380px){
.how_head span{width:55%;line-height: 18px;}
.please.ng-scope p{font-size:10px !important;}
.homeheader_img{background-size: 240px auto;padding: 30px 50px;}
}
