.pageTutorial ul{ padding-top:20px;}
.pageTutorial ul ul{ padding-top:0; padding-left:20px; color:#d7df20; padding-bottom:0;}
.pageTutorial li{ padding-bottom:20px;}
.pageTutorial li li{ padding-bottom:10px;}
.pageTutorial li li{ list-style:outside; list-style-type:disc;}
.pageTutorial li li p{color:#6e6f73; line-height:20px;}
.pageTutorial li .nos{ border-radius:50%; height:20px; background-color:#221f1f; color:#FFFFFF; width:20px; text-align:center; line-height:20px; display:inline-block; margin-right:5px;}
.pageTutorial li .title{ color:#007cc6; font-size:18px; line-height:20px; padding-bottom:10px;}

.pageTutorial .tutorialIn{ background-color:#e6e7e8; padding:20px; border-radius:10px;}
.pageTutorial .tutorialIn .col{ float:left; width:50%;}
.pageTutorial .tutorialIn .col.image{ padding-left:20px; text-align:right;}
.pageTutorial .tutorialIn .col.image img{ max-width:100%;}