.homeMenu {
    width: 100%;
    height: 100%;
    padding-top: 0px;
}

.homeMenu ul {
    width: 100%;
    height: 100%;
    padding: 20px 0 0 20px;
}

.homeMenu ul li {
    float: left;
    width: 50%;
    height: 50%;
    padding: 0 20px 20px 0;
}

.homeMenu ul li a {
    display: block;
    width: 100%;
    height: 100%;
    background-color: #d7df20;
    position: relative;
    color: #fff;
    overflow: hidden;
}

.homeMenu ul li a span {
    position: absolute;
    width: 100%;
    display: block;
    left: 0;
    text-align: center;
    top: 50%;
    margin-top: 45px;
}

.homeMenu ul li a.home,
.homeMenu ul li a.know_more,
.homeMenu ul li a.bmi_calculator,
.homeMenu ul li a.pedometer{
    color: #000;
}

.homeMenu ul li a.novomix_you,
.homeMenu ul li a.counselling,
.homeMenu ul li a.reminder,
.homeMenu ul li a.reports,
.homeMenu ul li a.counselling {
    background-color: #007cc1;
}

.homeMenu ul li a:hover {
    color: #D73F27;
}


