.homeMenu ul li a:before {
    content: '';
    width: 300px;
    height: 300px;
    border-radius: 50%;
    position: absolute;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    left: 50%;
    top: 50%;
    margin-left: -150px;
    margin-top: -150px;
    opacity: 0;
    /*border:150px solid rgba(215, 63, 39, 0.5);*/
    border: 150px solid rgba(215, 63, 39, 0.7);
}

.homeMenu ul li a:focus:before {
    -webkit-animation: spinAround 200ms ease-out 1;
    opacity: 0.7;
    -moz-animation: spinAround 200ms ease-out 1;
    animation: spinAround 200ms ease-out 1;
}



.homeMenu ul li a svg {
    position: absolute;
    background-repeat: no-repeat;
    background-position: center center;
    width: 100px;
    height: 100px;
    background-size: 100px 100px;
    display: block;
    left: 50%;
    margin-left: -50px;
    top: 50%;
    margin-top: -70px;
}

.homeMenu ul li a img {
    position: absolute;
    background-repeat: no-repeat;
    background-position: center center;
    width: 100px;
    height: 100px;
    background-size: 100px 100px;
    display: block;
    left: 50%;
    margin-left: -50px;
    top: 50%;
    margin-top: -70px;
}

.homeMenu ul li a polygon,
.homeMenu ul li a path {
    fill: #000;
}

.homeMenu ul li a.novomix_you polygon,
.homeMenu ul li a.counselling polygon,
.homeMenu ul li a.novomix_you path,
.homeMenu ul li a.counselling path {
    fill: #fff;
}

.homeMenu ul li a:hover polygon,
.homeMenu ul li a:hover path {
    fill: #D73F27;
}

.homeheader_img {
    background-color: #007cc1;
    background-image: url(../images/menu_head.png);
    margin: 20px 20px 0px 20px;
    display: block;
    padding: 50px;
    background-position: center;
    background-size: 500px;
    background-repeat: no-repeat;
}