footer{ position:fixed; height:auto; bottom:0; left:0; width:100%; background-color:#0d4084; z-index:100000;}
footer ul{height:100%;}
footer li{ float:left; width:25%; text-align:center;border-right:1px solid #64a3e8;height:100%;}
footer li:last-child{border:none;}
footer li a{ display:block; width:100%; height:100%; color:#fff;font-size:12px; position:relative;}
footer li a.active{ color:#D73F27;}
footer li span{ display:block;}

footer li a:before{ content:''; width:100px; height:100px; border-radius:50%; position:absolute; -webkit-transform: scale(0);
	-moz-transform: scale(0);
	-ms-transform: scale(0);
	transform: scale(0); left:50%; top:50%; margin-left:-50px; margin-top:-50px; opacity:0; border:50px solid rgba(255, 255, 255, 0.3);}
footer li a:focus:before{ -webkit-animation: spinAround 200ms ease-out 1; opacity:0.7;
	-moz-animation: spinAround 200ms ease-out 1;
	animation: spinAround 200ms ease-out 1;}

footer li svg{ width:40px; height:40px; margin:0 auto;}
footer li svg path, footer li svg polygon{fill:#fff;}
footer li a.active svg path, footer li a.active svg polygon{fill:#D73F27;}