.splashWrap{ height:100%; background-color:#ffffff; width:100%; position:fixed; left:0; top:0;}
.splashWrap .welcome{ text-align:center; color:#007cc1; text-transform:uppercase; position:absolute; top:50%; height:auto; width:100%; left:0;}
.splashWrap .welcome h2 {font-weight: 700;padding-top: 30px;display: inline-block;text-transform: none;font-size: 0;background-image: url(../images/logo_home.png);background-repeat: no-repeat;width: 100%;height: 100%;margin: auto;text-align: center;max-width: 310px;margin-top: 25px;background-size: 100%;}
.activationForm{ position:fixed; bottom:0; padding-bottom:20px; left:0; width:100%; background-color:#ffffff;}
.activationForm input[type="text"]{border-bottom:1px solid #007cc1; background-color:transparent; color:#007cc1; text-align:center; font-size:24px;}
.activationForm input[type="text"]:focus{border-bottom:1px solid #e0ea95;}
.activationForm label{ color:#007cc1; text-align:center;font-size: 18px;}
.splashWrap .logo{ height:50%; position:absolute; top:0; width:100%; text-align:center; left:0;}
.splashWrap .logo img{ max-width:600px; width:100%; position:absolute; bottom:0; left:50%; margin-left:-300px;}

.chooseLanguage{ position:absolute; width:300px; font-size:20px; left:50%; margin-left:-150px; top:50%; margin-top:-64px;}
.chooseLanguage label span{ display:block; width:100%; padding:10px 0; color:#7d9bd1;}
.chooseLanguage select{ width:100%; padding:10px;border:1px solid #000;}

.activationForm .error{ text-align:center;}
.logoText{ width:600px; height:346px; background:url(../images/logo.png) no-repeat center center; background-size:600px 346px; margin:0 auto; color:#FFFFFF; padding-top:50px; padding-left:80px; text-align:left; font-size:25px; font-weight:bold;}
.logoText .say, .logoText .yes, .logoText .toInsulin{ width:330px;}
.logoText .say{}
.logoText .yes{ font-size:100px; text-align:center; line-height:100px; height:120px;}
.logoText .toInsulin{ text-align:right;}

.welcome_con {padding-top: 30px;padding-bottom:30px;font-size: 22px;height:100%;}
.activationForm .submit input{background-color:#007cc1;}
